.document {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /* margin-top: 1rem; */
    background-color: #333;
}

.document button {
    width: 40px;
    height: 40px;
    font-family: inherit;
    font-weight: 600;
    font-size: 16px;
    margin: 0 10px;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
    background: #e1e1e1;
    color: #333;
    border-color: #101010;
}

.document button.active {
    font-weight: 900;
    border-color: #101010;
    background: #ffe400;
    color: #101010;
}