.unit {
  display: flex;
  flex-direction: row;
  padding: 1rem;
  flex-wrap: wrap;
  align-items: center;
  background-color: #333;
  height: 89.7vh;
 
}
.box > p {
  border: 1px solid #333;
  padding: 7px 7px;
  color: black;
  background-color: white;
  height: 250px;
  width: 230px;
  margin-left: 9rem;
  margin-top: 0.5rem;
  border-radius: 50%;
  text-align: center;
  box-shadow: 0px 5px 7px 0px  rgb(180, 180, 177);
}
img {
  border-radius: 50%;
  height: 165px;
}
.box > p > h4{
  align-items: center;
  border: 1px solid #333;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 23px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 5.5rem;
  margin-top: 0.7rem;
  background-color: #e1e1e1;
}